:host {
  display: block;
  position: relative;
  width: 100%;
  height: 100%;
}

.novel-left-menu {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 280px;
  z-index: 1002;
  margin: 0 !important;
  padding: 0 !important;
  background: var(--bg-primary);
  border-right: 1px solid var(--border-color);
  box-shadow: 2px 0 8px var(--shadow);
  animation: slideInFromLeft 0.3s ease-out;
  transition: width 0.3s ease;

  &.collapsed {
    width: 48px;
    overflow: hidden;
    
    .collapsed-view {
      display: flex;
      flex-direction: column;
      align-items: center;
      padding: 12px 0;
      height: 100%;
      overflow-y: auto;
      cursor: pointer;
      
      .icon-list {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 16px;
        width: 100%;
        
        .menu-icon {
          font-size: 24px;
          padding: 8px;
          text-align: center;
          transition: all 0.2s ease;
          border-radius: 8px;
          
          &:hover {
            background: var(--bg-secondary);
            transform: scale(1.1);
          }
        }
      }
    }
  }

  .main-menu,
  .expanded-panel {
    height: 100%;
    overflow-y: auto;
  }

  .panel-header {
    display: flex;
    align-items: center;
    gap: 12px;
    padding: 16px 20px;
    border-bottom: 1px solid var(--border-color);
    background: var(--bg-secondary);
    position: sticky;
    top: 0;
    z-index: 10;

    h3 {
      flex: 1;
      margin: 0;
      font-size: 18px;
      font-weight: 600;
      color: var(--text-primary);
    }

    .back-btn,
    .close-btn {
      background: none;
      border: none;
      color: var(--text-primary);
      font-size: 20px;
      cursor: pointer;
      padding: 4px 8px;
      border-radius: 4px;
      transition: background 0.2s ease;

      &:hover {
        background: var(--bg-tertiary);
      }
    }
  }

  .category-list {
    padding: 16px;
    
    .category-item {
      display: flex;
      align-items: center;
      gap: 16px;
      padding: 16px;
      margin-bottom: 8px;
      border-radius: 12px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-secondary);
        border-color: var(--border-color);
        transform: translateX(4px);
      }

      .category-icon {
        font-size: 24px;
        width: 40px;
        height: 40px;
        display: flex;
        align-items: center;
        justify-content: center;
        background: var(--bg-tertiary);
        border-radius: 10px;
      }

      .category-content {
        flex: 1;
        
        .category-title {
          font-size: 16px;
          font-weight: 600;
          color: var(--text-primary);
          margin-bottom: 4px;
        }
        
        .category-count {
          font-size: 12px;
          color: var(--text-secondary);
        }
      }

      .category-arrow {
        font-size: 18px;
        color: var(--text-secondary);
        transition: transform 0.2s ease;
      }

      &:hover .category-arrow {
        transform: translateX(4px);
      }
    }
  }

  .panel-items {
    padding: 16px;
    
    .menu-item {
      display: flex;
      align-items: center;
      gap: 12px;
      padding: 12px 16px;
      margin-bottom: 8px;
      border-radius: 8px;
      cursor: pointer;
      transition: all 0.2s ease;
      border: 1px solid transparent;

      &:hover {
        background: var(--bg-secondary);
        border-color: var(--border-color);
        transform: translateX(4px);
      }

      .item-content {
        flex: 1;
        
        .item-label {
          font-size: 14px;
          font-weight: 500;
          color: var(--text-primary);
          margin-bottom: 2px;
        }
        
        .item-description {
          font-size: 12px;
          color: var(--text-secondary);
          line-height: 1.3;
        }
      }

      .item-arrow {
        font-size: 14px;
        color: var(--text-secondary);
        transition: transform 0.2s ease;
      }

      &:hover .item-arrow {
        transform: translateX(2px);
      }
    }
  }
}

@keyframes slideInFromLeft {
  from {
    transform: translateX(-100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

// 반응형 처리
@media (max-width: 768px) {
  .novel-left-menu {
    width: 260px;
    
    &.collapsed {
      width: 44px;
    }
    
    .panel-header {
      padding: 12px 16px;
      
      h3 {
        font-size: 16px;
      }
    }
    
    .category-list,
    .panel-items {
      padding: 12px;
    }
    
    .category-item {
      padding: 12px;
      
      .category-icon {
        width: 36px;
        height: 36px;
        font-size: 20px;
      }
      
      .category-content .category-title {
        font-size: 14px;
      }
    }
  }
}